/****************************************************************************
 * Review request box
 ****************************************************************************/
.review-request {
  background: none;
  border-radius: 6px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15);
}
.review-request textarea {
  border: 1px #999999 solid;
  height: 1.5rem;
  overflow: hidden;
}
.review-request .inline-editor-form input[type='text']:not(:focus) {
  box-shadow: 0px 0px 4px rgba(255, 0, 0, 0.75);
}
.review-request .required-flag {
  color: #CB0000;
  font-size: 10px;
  font-weight: bold;
  padding: 0 2px;
}
.review-request-header {
  border-bottom: 1px #a88d32 solid;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-header {
    text-align: center;
  }
}
.review-request-body {
  background-color: #FEFADF;
  background-repeat: repeat-x;
  background-image: linear-gradient(to bottom, #fffdf3, #FEFADF);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fffdf3', endColorstr='#FEFADF', GradientType=0);
  border: 1px #9E9A7F solid;
  border-top: 0;
  border-radius: 0 0 6px 6px;
  clear: both;
  padding: 0.5rem;
}
.review-request-body .field-container {
  position: relative;
}
.banner pre.field,
.review-request-body pre.field {
  background-color: white;
  border: 1px #999999 solid;
  margin: 10px 0 0 0;
  padding: 10px;
  white-space: pre-wrap;
}
.banner pre.field .codehilite,
.review-request-body pre.field .codehilite {
  border-left: 0;
  border-right: 0;
  border-radius: 0;
  margin: -4px -10px;
}
.commit-list {
  background-color: #FFFFFF;
  border: 1px #999999 solid;
  border-collapse: collapse;
  margin: 10px 0 0 0;
  display: table !important;
  width: 100%;
}
.commit-list.changed .marker {
  font-family: monospace;
  font-size: 13px;
  font-weight: bold;
  text-align: center;
  width: 0;
}
.commit-list.changed .new-value .marker {
  background: #caf2c1;
}
.commit-list.changed .old-value .marker {
  background: #f5d0d6;
}
.commit-list .collapse-commit-message,
.commit-list .expand-commit-message {
  color: #000000 !important;
}
.commit-list .select-base,
.commit-list .select-tip {
  width: 0;
}
.commit-list pre {
  margin: 0;
}
.commit-list td,
.commit-list th {
  border-bottom: 1px #DDDDDD solid;
  font-family: Verdana, Arial, Helvetica, Sans-Serif !important;
  padding: 0.25em 0.5em;
  vertical-align: baseline;
}
.commit-list th {
  text-align: left;
}
.commit-list tr:last-child td {
  border-bottom: none;
}
.commit-list .expand-collapse-control {
  width: 2em;
}
.commit-list .value {
  width: 100%;
}
.review-request-section {
  padding: 1em;
  position: relative;
}
.review-request-section-label {
  color: #575012;
  font-size: 13px;
  font-weight: bold;
}
.review-request-summary p {
  margin: 1em 0 0 0;
}
.review-request-summary #field_summary {
  display: inline;
}
.review-request-summary #field_summary a {
  color: black;
  text-decoration: none;
}
.review-request-summary .review-request-section-label,
.review-request-summary #field_summary {
  font-size: 14px;
}
.review-request-summary .review-request-section-label {
  /* Hide the "Summary:" label on mobile. */
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
