    display: none;
  }
}
.review-request-summary .rb-icon-edit {
  vertical-align: text-bottom;
}
.review-request-main-fieldset {
  display: block;
  margin-right: 310px;
  position: relative;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-main-fieldset {
    margin-right: 0;
  }
}
#review-request-main {
  display: block;
  margin-right: 310px;
  position: relative;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  #review-request-main {
