/****************************************************************************
 * Review request actions
 ****************************************************************************/
.review-request-actions-container {
  background: #E5D7A8;
  border-color: #a88d32;
  border-radius: 5px 5px 0 0;
  border-style: solid;
  border-width: 1px 1px 0 1px;
  box-sizing: border-box;
  min-height: 26px;
  overflow: hidden;
}
.review-ui-box .review-request-actions-container {
  border-radius: 5px;
}
.review-ui-box.has-review-ui-box-content .review-request-actions-container {
  border-radius: 5px 5px 0 0;
}
.review-request-actions-left {
  float: left;
}
.review-request-actions-right-container,
.review-request-actions-right {
  float: right;
}
.review-request-actions {
  box-sizing: border-box;
  list-style: none;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}
.review-request-action {
  float: left;
}
.review-request-action:hover {
  background-color: #d7c179;
}
.review-request-action:active {
  background-color: #ceb359;
}
.review-request-action a {
  color: black;
  cursor: pointer;
  display: block;
  margin: 0;
  line-height: 16px;
  text-decoration: none;
  padding: 5px 10px;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-action a {
    /* Give some extra room for tapping. */
    padding: 10px 10px;
  }
}
.review-request-action .menu {
  background: #ceb359;
  border: 1px #a88d32 solid;
  border-radius: 0 0 6px 6px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15);
  list-style: none;
  margin: 0;
  float: none;
}
.review-request-action .menu li {
  background: #E5D7A8;
  border: 0;
  float: none;
  margin: 0;
  padding: 0;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-action .menu li {
    /* Give some extra room for tapping. */
    padding: 10px;
  }
}
.review-request-action .menu li:last-child {
  border-radius: 0 0 6px 6px;
}
.review-request-action .menu li:hover {
  background-color: #d7c179;
}
/*
 * Disable some confusing cursors and interaction when the site is in
 * read-only mode.
 */
body.read-only .review-request-action-archive:active,
body.read-only .review-request-action-star:active,
body.read-only .review-request-action-archive:hover,
body.read-only .review-request-action-star:hover {
  background-color: inherit;
}
body.read-only .review-request-action-archive > a,
body.read-only .review-request-action-star > a {
  cursor: default;
}
.review-request-action-icon {
  display: inline-block;
}
.review-request-action-icon > a:first-child {
  line-height: 0;
  padding: 5px 5px;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-action-icon > a:first-child {
    padding: 10px;
  }
}
/*
 * The main text-based actions turn into a drop-down menu on mobile.
 *
 * The following rules do several things:
 * - On desktop, we don't want to have any kind of hover/active highlighting
 *   on the review-request-actions-right-container (which is itself an "action"
 *   due to it's menu-ness).
 * - Hide the mobile actions menu "..." icon on desktop, show on mobile.
 * - Turn .review-request-actions-right into a drop-down menu, but a special
 *   one that lists its items horizontalli (with wrapping), rather than a
 *   vertical layout.
 */
.review-request-actions-right-container > .review-request-action:hover {
  background-color: inherit;
}
a.mobile-actions-menu-label {
  display: none;
}
@media screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) {
  a.mobile-actions-menu-label {
    display: inline-block;
  }
  .review-request-actions-right-container > .review-request-action:hover {
    background-color: #d7c179;
  }
  .review-request-actions-right {
    background: #FFFFFF;
    border: 1px #C0C0C0 solid;
    box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.05);
    display: block;
    opacity: 0;
    padding: 0;
    position: absolute;
    transition: opacity 0.2s linear;
    visibility: hidden;
    z-index: 110;
    /**
   * A menu item.
   *
   * This may contain any content needed in the menu item, but will usually
   * consist of text.
   *
   * The element type may be anything, and will depend on the parent
   * ``.rb-c-menu``. It will most commonly be a ``<li>``, ``<a>``, ``<div>``,
   * or ``<button>``.
   *
   * If displaying a button-based menu, this must have ``.rb-c-button`` mixed
   * in.
   *
   * Consumers are responsible for marking a menu item as focused in response
   * to keyboard navigation or mouse movements, in order to convey the active
   * item.
   *
   * DOM Attributes:
   *     role (required):
   *         This must be set to "menuitem".
   *
   *     tabindex (required):
   *         This must be set to -1, allowing the menu to be focused
   *         programmatically but not through tabbing. Consumers should provide
   *         keep
   *
   * Structure:
   *     <li class="rb-c-menu__item" role="menuitem" tabindex="-1">...</li>
   *
   *     <a class="rb-c-menu__item" role="menuitem" tabindex="-1">...</a>
   *
   *     <button class="rb-c-menu__item rb-c-button" role="menuitem"
   *             type="button" tabindex="-1">
   *      ...
   *     </button>
   */
    background: #ceb359;
    border: 1px #a88d32 solid;
    border-radius: 0 0 6px 6px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.15);
    list-style: none;
    margin: 0;
    background: #E5D7A8;
    left: 0;
    white-space: normal;
    width: 100%;
  }
  .review-request-actions-right.-is-open {
    opacity: 1;
    visibility: visible;
  }
  .review-request-actions-right.js-no-animation {
    transition: none;
  }
  .review-request-actions-right__item {
    cursor: pointer;
    display: block;
    list-display: none;
    margin: 0;
  }
  .review-request-actions-right__item.rb-c-button:focus {
    background: #DAEBFF;
    outline: 0;
  }
  .review-request-actions-right__item:not(.rb-c-button) {
    /*
       * We want to apply padding to standard list items, but button list
       * items should retain button padding.
       */
    padding: 0.8em 1em;
  }
  .review-request-actions-right__item:not(.rb-c-button):focus {
    background: #DAEBFF;
    outline: 0;
  }
  .review-request-actions-right li {
    background: #E5D7A8;
    border: 0;
    float: none;
    margin: 0;
    padding: 0;
  }
  .review-request-actions-right li:last-child {
    border-radius: 0 0 6px 6px;
  }
  .review-request-actions-right li:hover {
    background-color: #d7c179;
  }
  .review-request-actions-right > li {
    display: inline-block;
  }
  .has-menu:hover .review-request-actions-right {
    opacity: 1;
    visibility: visible;
  }
}
@media screen and (max-width: 720px) and screen and (max-width: 720px), screen and (max-device-width: 720px) and (orientation: landscape) and screen and (max-width: 720px), screen and (max-width: 720px) and screen and (max-device-width: 720px) and (orientation: landscape), screen and (max-device-width: 720px) and (orientation: landscape) and screen and (max-device-width: 720px) and (orientation: landscape) {
  .review-request-actions-right li {
    /* Give some extra room for tapping. */
    padding: 10px;
  }
}
/****************************************************************************
 * Review request tabs
 ****************************************************************************/
.review-request-tabs {
  background-color: #E5D7A8;
  border-color: #9E9A7F;
  border-radius: 6px 6px 0 0;
  border-style: solid;
  border-width: 1px 1px 0 1px;
  float: right;
  margin-left: 1.5rem;
  overflow: hidden;
}
.review-ui-box .review-request-tabs .active {
  /*
     * The active tab in review UIs has a slightly different color to match the
     * review UI header color.
     */
  background-color: #FEFADF;
}
.review-request-tabs li {
  border-bottom: 1px #a88d32 solid;
  min-width: 4rem;
  text-align: center;
}
.review-request-tabs li.active {
  background: #fffdf3;
  border-color: #9E9A7F;
  border-bottom-color: transparent;
  border-style: solid;
  border-width: 0 1px 1px 1px;
}
.review-request-tabs li.active:first-child {
  border-left: 0;
}
.review-request-tabs li.active:last-child {
  border-right: 0;
}
